import axios from './request'
import QS from 'qs'

const baseUrl = 'https://gateway.mdex.com/v2/'

function request (url: string, params: any, type: string) {
  let param = {}
  if (type === 'get') {
    param = {
      url: url + '?' + QS.stringify(params),
      method: type
    }
  } else {
    param = {
      url: url,
      method: type,
      data: QS.stringify(params)
    }
  }
  return new Promise((resolve, reject) => {
    axios(param).then(({data}) => {
      if (data.code === 0) {
        resolve(data.result ? data.result.data : data.data)
      } else {
        reject(data)
      }
    }).catch(error => {
      reject(error)
    })
  })
}

export function GET_COLLECT_LIST_ACTION(data: any) {
  return request(baseUrl + 'quotation/collect/list', data, 'get')
}

export function ADD_COLLECT_ACTION(data: any) {
  return axios({
      url: baseUrl + 'quotation/collect/add',
      method: 'post',
      data: QS.stringify(data)
  })
}

export function REMOVE_COLLECT_ACTION(data: any) {
  return axios({
      url: baseUrl + 'quotation/collect/cancel',
      method: 'post',
      data: QS.stringify(data)
  })
}

