import historyProvider from './historyProvider'
import {
  LibrarySymbolInfo,
  HistoryCallback,
  ErrorCallback,
	OnReadyCallback,
	ResolutionString,
	ResolveCallback,
	Timezone,
	SeriesFormat,
	SubscribeBarsCallback
} from '../../../charting_library'
import { GET_KLINE_ACTION } from '../../../utils/api';

const supportedResolutions = ['1', '5', '15', '30', '60', "D", "W", "1M"] as ResolutionString[]

const config = {
    supported_resolutions: supportedResolutions
}; 
let lastTime = 0
function getKLineData () {
	const token = getParms(window.location.href)
	return new Promise((resolve, reject) => {
		GET_KLINE_ACTION({
			pair_token: token,
			period: 60,
			limit: 1
		}).then((list: any) => {
			let item = list[0]
			if (list.length && item[0] > lastTime) {
				lastTime = item[0]
				resolve({
					time: item[0],
					open: parseFloat(item[1]),
					high: parseFloat(item[2]),
					low: parseFloat(item[3]),
					close: parseFloat(item[4]),
					volume: parseFloat(item[5])
				})
			}
			resolve(null)
		}).catch(() => {
			resolve(null)
		})
	})
}
let onResetCallback = () =>{}
let time: any = null
export default {
	onReady: (configurationData: OnReadyCallback) => {
		setTimeout(() => configurationData(config), 0)
		
	},
  // 通过商品名称解析商品信息
	resolveSymbol: (symbolName: string, onSymbolResolvedCallback: ResolveCallback) => {
		console.log('======resolveSymbol running', symbolName)
		const symbol_stub = {
			name: symbolName,
			description: '',
			type: 'crypto',
			session: '24x7',
			ticker: symbolName,
			exchange: '',
			minmov: 1,
			pricescale: 100000000,
			has_intraday: true,
			intraday_multipliers: ['1', '60'],
			// volume_precision: 2,
			// pricescale: Math.pow(10, pricePrecision > 7 ? 7:pricePrecision),
			// data_status: 'streaming',
			pro_name: symbolName,
			has_daily: true,
			has_weekly_and_monthly: true,
			timezone: 'Asia/Chongqing' as Timezone,
			full_name: symbolName,
			listed_exchange: '',
			format: 'price' as SeriesFormat,
			supported_resolutions: supportedResolutions
		}
		setTimeout(function() {
			onSymbolResolvedCallback(symbol_stub)
		}, 0)
	},
	getBars: function(symbolInfo: LibrarySymbolInfo, resolution: string, from: number, to: number, onHistoryCallback: HistoryCallback, onErrorCallback: ErrorCallback, firstDataRequest: boolean) {
		console.log('getBars-----')
		const token = getParms(window.location.href)
		if (from > 0 && to > 0) {
			const localResolutions = localStorage.getItem('localResolutions') || resolution
			historyProvider.getBars(symbolInfo, localResolutions, from, to, firstDataRequest, token)
			.then((bars: any) => {

				const len = bars.length
				lastTime = bars[len -1].time
				if (len) {
					if (to*1000 > lastTime) {
						onHistoryCallback(bars, {noData: false})
					} else {
						onHistoryCallback([], {noData: true})
					}
				} else {
					onHistoryCallback(bars, {noData: true})
				}
			}).catch(err => {
				// console.log({err})
				onErrorCallback(err)
			})
		}
	},
	subscribeBars: (symbolInfo: LibrarySymbolInfo, resolution: ResolutionString, onRealtimeCallback: SubscribeBarsCallback, subscribeUID: string, onResetCacheNeededCallback: () => void) => {
		console.log('subscribeBars-----')
		clearTimeout(time)
		const subscribeData = () => {
			time = setTimeout(() => {
				getKLineData().then((data: any) => {
					if (data) {
						onRealtimeCallback(data)
					}
					subscribeData()
				})
			}, 2000);
		}
		subscribeData()
		onResetCallback = onResetCacheNeededCallback
	},
	unsubscribeBars(subscriberUID: string) {
		console.log('unsubscribeBars--')
		clearTimeout(time)
	},
  // 图表库请求一些历史数据的时候会调用这个函数，能够覆盖所需的历史深度
	calculateHistoryDepth: (resolution: string, resolutionBack: string, intervalBack: number | string) => {
		console.log('=====calculateHistoryDepth running', resolution, resolutionBack, intervalBack)
	}
}

export function onCallback () {
	onResetCallback()
}

function getParms (href: string) {
	let list = href.split('exchange')[1]
	if (!list) {
		list = href.split('info')[1]
	}
	let token = list && list.split('/')[1]
	return token
}