import styled from 'styled-components'
import { useHistory, useParams } from 'react-router'
import { useEffect, useState } from 'react'

import { Flex } from '../../../../components/Row'
import Quotes from '../../quotes'

import backIcon from '../images/back.png'
import collectIcon from '../images/collect.png'
import collectedIcon from '../images/collected.png'
import listIcon from '../images/list.png'
import settingIcon from '../images/setting.png'

import { GET_COLLECT_LIST_ACTION, ADD_COLLECT_ACTION, REMOVE_COLLECT_ACTION } from '../../../../utils/api'

const Modal = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding-right: 40px;
  background: rgba(0, 0, 0, 0.4);
  overflow-y: scroll;
  z-index: 1000;
`
const Split = styled.div`
  width: 1px;
  height: 14px;
  margin: 0 10px;
  background-color: #8491B3;
  border: 1px solid #8491B3;
`
const Text = styled.span`
  margin-left: 10px;
  font-size: 20px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: #10283F;
`
const ACCOUNT = '0x99D0aDb61C345fF9545D17E8b4Da16B375D5fc00'

export default function Head ({ title, showList, showCollect, showSetting } : { title: string, showList?: boolean, showCollect?: boolean, showSetting?: boolean }) {
  const history = useHistory()
  const { id } = useParams<{id?: string}>()
  const [state, setstate] = useState(false)
  const [list, setList] = useState(false)

  useEffect(() => {
    getCollect()
  }, [])

  const handleClick = function () {
    setList(!list)
  }

  function getCollect () {
    GET_COLLECT_LIST_ACTION({
      account: ACCOUNT
    }).then((data: any) => {
      data.map((item: any) => {
        if (item.address === id) {
          setstate(true)
        }
      })
    })
  }
  const handleCollect = () => {
    if(state) {
      // 取消收藏
      REMOVE_COLLECT_ACTION({
        account: ACCOUNT,
        pair_token: id
      }).then (({data}) => {
        if (data.code === 0) {
          setstate(false)
        }
      })
    } else {
      // 添加收藏
      ADD_COLLECT_ACTION({
        account: ACCOUNT,
        pairs: [
          {
            pair_name: title,
            address: id
          }
        ]
      }).then(({data}) => {
        if (data.code === 0) {
          setstate(true)
        }
      })
    }
  }
  return (
    <>
      <Flex width='100%' justify='space-between' padding='14px 15px 14px 13px'>
        <Flex >
          <img onClick={() => history.goBack()} width='20px' src={backIcon} alt="back"/>
          {showList && <>
            <Split></Split>
            <img width='20px' onClick={handleClick} src={listIcon} alt="list"/>
          </>}
          <Text>{title}</Text>
        </Flex>
        <div style={{fontSize: '0'}}>
          {showSetting && <img onClick={() => history.push(`/info/${id}`)} width='20px' src={settingIcon} style={{marginRight: '10px'}} alt="setting"/>}
          {showCollect && <img onClick={handleCollect} width='21px' src={state ? collectedIcon : collectIcon} alt="collect"/>}
        </div>
      </Flex>
      {list && <Modal onClick={handleClick}>
        <Quotes type='component' hideMenu></Quotes>
      </Modal>}
    </>
  )
}