import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'
import { useMedia } from 'react-use'
import { Box, Flex } from 'rebass'

import DoubleTokenLogo from './DoubleTokenLogo'
import Row from '../../../components/Row'

import { CustomLink } from './Link'
import { formattedNum } from '../../../utils'
import Loading from '../../../components/Loading'
import TableHead from './TableHead'

dayjs.extend(utc)

const List = styled(Box)`
  -webkit-overflow-scrolling: touch;
  background: #fff;
`

const DashGrid = styled.div`
  display: grid;
  grid-gap: 0.2em;
  grid-template-columns: 1fr 1.2fr 1fr;
  grid-template-areas: 'name liq vol';
  padding: 0 1.125rem;
  border-bottom: 1px solid #F1F4F9;
  cursor: pointer;

  > * {
    justify-content: flex-end;

    &:first-child {
      justify-content: flex-start;
      text-align: left;
    }
  }

  @media screen and (min-width: 1080px) {
    display: grid;
    grid-gap: 0.5em;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: 'name symbol liq vol price change';
  }
`

const DataText = styled(Flex)`
  align-items: center;
  text-align: center;
  color: ${({ theme }) => theme.text2} !important;
  font-size: 14px;

  @media screen and (max-width: 600px) {
    font-size: 12px;
  }
`
const StyleItem = styled.div`
  position: relative;
  z-index: 100;
  &:hover {
    width: 1236px;
    height: 100%;
    padding: 0 18px;
    margin-left: -18px;
    background-color: #fff;
    box-shadow: 0px 10px 20px 0px rgba(10, 20, 111, 0.1);
    border-radius: 10px;
  }
`

const SORT_FIELD = [
  {
    key: 'Account',
    value:'Account'
  },
  {
    key: 'Pair',
    value:'Pair',
    disabled: true
  },
  {
    key: 'usd',
    value:'Price'
  }
]

function filterText(value: string, search: string) {
  return value.indexOf(search.toLocaleUpperCase()) > -1
}

// @TODO rework into virtualized list
function AccountTable({ list, page, search, setMaxPage } : {
  list: any,
  page: number,
  search: string,
  useTracked?: boolean,
  setMaxPage: React.Dispatch<number>
}) {
  const itemMax = 10
  // sorting
  const [sortDirection, setSortDirection] = useState(true)
  const [sortedColumn, setSortedColumn] = useState('usd')
  const [filterList, setFilterList] = useState([])

  const below800 = useMedia('(max-width: 800px)')

  useEffect(() => {
    if (list) {
      let extraPages = 1
      if (Object.keys(list).length % itemMax === 0) {
        extraPages = 0
      }
      setMaxPage(Math.floor(Object.keys(list).length / itemMax) + extraPages)
    }
  }, [list, itemMax])

  const handleAccount = (account: string) => {
    window.open('https://bscscan.com/address/' + account)
  }

  const ListItem = ({ item, index } : { item: any, index: number }) => {
    return (
      <DashGrid style={{ height: '67px' }}>
        <DataText>
          <Row>
            {!below800 && <div style={{ marginRight: '1rem', width: '10px' }}>{index}</div>}
            <div onClick={() => handleAccount(item.user.id)} style={{ marginLeft: below800 ? '0' : '20px', color: '#2f80ed', whiteSpace: 'nowrap' }}>
              {below800 ? item.user.id.slice(0, 4) + '...' + item.user.id.slice(38, 42) : item.user.id}
            </div>
          </Row>
        </DataText>
        <DataText color="text">
          <DoubleTokenLogo
            size={below800 ? 16 : 30}
            a0={item.token0}
            a1={item.token1}
            margin={!below800}/>
          <DataText style={{marginLeft: '20px'}}>{item.pairName}</DataText>
        </DataText>
        <DataText>{formattedNum(item.usd, true)}</DataText>
      </DashGrid>
    )
  }

  useEffect(() => {
    const ListElement =
    list && list.filter((item: any) => {
      return filterText(item.pairName, search)
    }).sort((a: any, b: any) => {
      return parseFloat(a[sortedColumn]) > parseFloat(b[sortedColumn])
        ? (sortDirection ? -1 : 1) * 1
        : (sortDirection ? -1 : 1) * -1
    }).slice(itemMax * (page - 1), page * itemMax)
    setFilterList(ListElement || [])
  }, [list, sortedColumn, page, sortDirection, search])

  return (
    <>
      <TableHead
        columns='1fr 1fr 1fr'
        SORT_FIELD={SORT_FIELD}
        sortedColumn={sortedColumn}
        sortDirection={sortDirection}
        setSortedColumn={setSortedColumn}
        setSortDirection={setSortDirection}
      ></TableHead>
      <List p={0}>
        {!filterList.length ? <Loading height='180px'/> : filterList.map((item: any, index: number) => {
          return (
            <StyleItem key={index}>
              <ListItem key={index} index={(page - 1) * 10 + index + 1} item={item} />
            </StyleItem>
          )
        })}
      </List>
    </>
  )
}

export default AccountTable
