import styled from 'styled-components'

import { RowBetween } from '../../Row'
import Button from '../../Button'
import CheckImg from './share.png'
import { useHistory } from 'react-router'

const Flex = styled.div`
  display: flex;
`
const Wrapper = styled.div`
  position: relative;
  width: 100%;
  border-bottom: 1px solid #B7C3DC;
`

const TextWrapper = styled.div`
  position: relative;
  line-height: 50px;
  margin-right: 20px;
  font-size: 18px;
  color: #8491B3;
  &.active {
    color: #10283F;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #03AD90;
    }
  }
`

const Search = styled.input`
  width: 67px;
  height: 24px;
  padding: 4px 7px;
  margin-left: 10px;
  font-size: 12px;
  color: #7F8FAF;
  background-color: #F5F7F9;
  border: none;
  border-radius: 4px;
  outline: none;
`

export default function Header({ state, setSearch, setstate }: { state: number, setSearch: React.Dispatch<string>, setstate: React.Dispatch<number> }) {
  const history = useHistory()

  const handleChange = (e: any) => {
    let value: string = e.target.value || ''
    setSearch(value)
  }

  return(
    <Wrapper>
      <RowBetween padding="0px 16px 0 10px" height="50px">
        <Flex>
          <TextWrapper className={state === 0 ? 'active' : ''} onClick={() => setstate(0)}>自选</TextWrapper>
          <TextWrapper className={state === 1 ? 'active' : ''} onClick={() => setstate(1)}>现货</TextWrapper>
        </Flex>
        <Flex>
          <Button onClick={() => history.push('/market')} fontSize="12px" padding="3px 6px">
            <span>查看info</span>
            <img width="12px" src={CheckImg} alt="check"/>
          </Button>
          <Search placeholder="搜索" onChange={(e) => handleChange(e)}></Search>
        </Flex>
      </RowBetween>
    </Wrapper>
  )
}