import { createContext, useCallback, useContext, useEffect, useMemo, useReducer } from 'react'
import { client } from '../apollo/client'
import {
  USER_TRANSACTIONS
} from '../apollo/queries'
import { connect } from '../utils/publicErc20'

const TRANSACTIONS_KEY = 'TRANSACTIONS_KEY'
const UPDATE_TRANSACTIONS = 'UPDATE_TRANSACTIONS'
const UPDATE_ACCOUNT = 'UPDATE_ACCOUNT'

const UserContext = createContext('user')

function useUserContext() {
  return useContext(UserContext)
}

// 获取我的成交记录
export function useUserTransactions(account: string) {
  // @ts-ignore
  const [state, { updateTransactions }] = useUserContext()
  const transactions = state?.[account]?.[TRANSACTIONS_KEY]
  useEffect(() => {
    async function fetchData(account: string) {
      try {
        let result = await client.query({
          query: USER_TRANSACTIONS,
          variables: {
            user: account,
          },
          fetchPolicy: 'no-cache',
        })
        if (result?.data) {
          updateTransactions(account, result?.data)
        }
      } catch (e) {
        console.log(e)
      }
    }
    if (!transactions && account) {
      fetchData(account)
    }
  }, [account, transactions, updateTransactions])

  return transactions || {}
}

export function useUserAccount() {
  // @ts-ignore
  const [state, { updateAccount }] = useUserContext()
  const account = state?.account
  useEffect(() => {
    if (!account) {
      connect().then(address => {
        updateAccount(address[0])
      })
    }
  }, [account])
  return account || ''
}


const INITIAL_STATE = {}

export default function Provider({ children } : { children: any }) {
  const [state, dispatch] = useReducer(reducer, INITIAL_STATE)

  const updateTransactions = useCallback((account, transactions) => {
    dispatch({
      type: UPDATE_TRANSACTIONS,
      payload: {
        account,
        transactions,
      },
    })
  }, [])

  const updateAccount = useCallback((account) => {
    dispatch({
      type: UPDATE_ACCOUNT,
      payload: {
        account
      },
    })
  }, [])

  return (
    // @ts-ignore
    <UserContext.Provider value={useMemo(
        () => [
          state,
          { updateTransactions, updateAccount },
        ],
        [state, updateTransactions, updateAccount]
      )}
    >
      {children}
    </UserContext.Provider>
  )
}

function reducer(state: any, { type, payload } : { type: string, payload: any }) {
  switch (type) {
    case UPDATE_TRANSACTIONS: {
      const { account, transactions } = payload
      return {
        ...state,
        [account]: {
          ...state?.[account],
          [TRANSACTIONS_KEY]: transactions,
        },
      }
    }
    case UPDATE_ACCOUNT: {
      const { account } = payload
      return {
        ...state,
        account
      }
    }

    default: {
      throw Error(`Unexpected action type in DataContext reducer: '${type}'.`)
    }
  }
}