import styled from 'styled-components'
import { Slider } from 'antd';

const Wrapper = styled.div`
  margin-top: 15px;
  position: relative;
  width: 100%;
  .ant-slider-rail {
    background: #171F41;
  }
  .ant-slider-step {
  }
  .ant-slider:hover .ant-slider-rail {
  background-color: #171F41;
  }
  .ant-slider:hover .ant-slider-track {
    background-color: #fff;
  }
  .ant-slider:hover .ant-slider-handle:not(.ant-tooltip-open) {
    border-color: #fff;
  }
  .ant-slider-mark-text {
    display: none;
  }
  .ant-slider-dot {
    top: -4px;
    width: 12px;
    height: 12px;
    margin-left: -6px;
    background-color: #171F41;
    border: 2px solid #060C24;
  }
  .ant-slider-dot-active {
    background-color: #fff;
  }
  .ant-slider-track {
    background: #fff;
  }
  .ant-slider-handle {
    margin-top: -5px;
    width: 6px;
    height: 6px;
    background-color: #000;
    border: solid 6px #fff;
  }
`

const marks = {
  0: '0%',
  25: '25%',
  50: '50%',
  75: '75%',
  100: '100%'
}

export default function Progress ({ setPercent, percent, setFlag } : {
  percent: number,
  setPercent: React.Dispatch<number>,
  setFlag: React.Dispatch<boolean>
}) {
  function onChange (value: number) {
    setFlag(false)
    setPercent(value)
  }
  function tipFormatter (value: any) {
    return value + '%'
  }
  return (
    <Wrapper>
      <Slider
        onChange={onChange}
        tipFormatter={tipFormatter}
        value={percent}
        defaultValue={0}
        marks={marks}/>
    </Wrapper>
  )
}