import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'
import { useMedia } from 'react-use'
import { Box, Flex } from 'rebass'

import { formatTime, formattedNum, updateNameData } from '../../../utils'
import TableHead from './TableHead'
import TabNav from '../../exchange/components/TabNav'

dayjs.extend(utc)

const List = styled(Box)`
  -webkit-overflow-scrolling: touch;
  background: #fff;
`

const DashGrid = styled.div`
  display: grid;
  grid-gap: 1em;
  grid-template-columns: 100px 1fr 1fr;
  grid-template-areas: 'name liq vol';
  padding: 0 1.125rem;
  border-bottom: 1px solid #F1F4F9;
  cursor: pointer;

  > * {
    justify-content: flex-end;

    &:first-child {
      justify-content: flex-start;
      text-align: left;
      width: 100px;
    }
  }

  @media screen and (min-width: 680px) {
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 1.2fr 1fr 1fr 1fr;
    grid-template-areas: 'name symbol liq vol ';

    > * {
      justify-content: flex-end;
      width: 100%;

      &:first-child {
        justify-content: flex-start;
      }
    }
  }

  @media screen and (min-width: 1080px) {
    display: grid;
    grid-gap: 0.5em;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas: 'name symbol liq vol price change';
  }
`

const DataText = styled(Flex)`
  align-items: center;
  text-align: center;
  color: ${({ theme }) => theme.text2} !important;
  font-size: 14px;

  @media screen and (max-width: 600px) {
    font-size: 12px;
  }
`
const StyleItem = styled.div`
  position: relative;
  z-index: 100;
  &:hover {
    width: 1236px;
    height: 100%;
    padding: 0 18px;
    margin-left: -18px;
    background-color: #fff;
    box-shadow: 0px 10px 20px 0px rgba(10, 20, 111, 0.1);
    border-radius: 10px;
  }
`

const SORT_FIELD = [
  {
    key: 'Name',
    value:'Name'
  },
  {
    key: 'amountUSD',
    value:'Toal Value'
  },
  {
    key: 'token0Amount',
    value:'Token Amount'
  },
  {
    key: 'token1Amount',
    value:'Token Amount'
  },
  {
    key: 'Account',
    value:'Account',
    disabled: true
  },
  {
    key: 'timestamp',
    value:'Time'
  }
]
const SORT_FIELD_APP = [
  {
    key: 'Name',
    value:'Name'
  },
  {
    key: 'Toal Value',
    value:'Toal Value'
  },
  {
    key: 'Time',
    value:'Time'
  }
]

const TXN_TYPE = {
  ALL: 'All',
  SWAP: 'Swaps',
  ADD: 'Adds',
  REMOVE: 'Removes',
}

function getTransactionType(event: string, symbol0: string, symbol1: string) {
  const formattedS0 = symbol0?.length > 8 ? symbol0.slice(0, 7) + '...' : symbol0
  const formattedS1 = symbol1?.length > 8 ? symbol1.slice(0, 7) + '...' : symbol1
  switch (event) {
    case TXN_TYPE.ADD:
      return formattedS0 + '-' + formattedS1
    case TXN_TYPE.REMOVE:
      return formattedS0 + '-' + formattedS1
    case TXN_TYPE.SWAP:
      return formattedS0 + '-' + formattedS1
    default:
      return ''
  }
}

function filterText(value: string, search: string) {
  return value.indexOf(search.toLocaleUpperCase()) > -1
}

// @TODO rework into virtualized list
function TransactionsTable({ list, useTracked = false, page, search, setMaxPage } : {
  list: any,
  page: number,
  useTracked?: boolean,
  search: string,
  setMaxPage: React.Dispatch<number>
}) {
  const [filteredItems, setFilteredItems] = useState([])
  const [sortDirection, setSortDirection] = useState(true)
  const [sortedColumn, setSortedColumn] = useState('timestamp')
  const [txFilter, setTxFilter] = useState(TXN_TYPE.ALL)
  
  const transactionNavList = ['All', 'Swaps', 'Adds', 'Removes']
  const MAX_COUNT = 10
  const below1080 = useMedia('(max-width: 1080px)')
  const below800 = useMedia('(max-width: 800px)')
  

  useEffect(() => {
    if (list && list.mints && list.burns && list.swaps) {
      let newTxns: any[] = []
      if (list.mints.length > 0) {
        list.mints.map((mint: any) => {
          let newTxn: any = {}
          newTxn.hash = mint.transaction.id
          newTxn.timestamp = mint.transaction.timestamp
          newTxn.type = TXN_TYPE.ADD
          newTxn.token0Amount = mint.amount0
          newTxn.token1Amount = mint.amount1
          newTxn.account = mint.to
          newTxn.token0Symbol = updateNameData(mint.pair)?.token0?.symbol
          newTxn.token1Symbol = updateNameData(mint.pair)?.token1?.symbol
          newTxn.amountUSD = mint.amountUSD
          return newTxns.push(newTxn)
        })
      }
      if (list.burns.length > 0) {
        list.burns.map((burn: any) => {
          let newTxn: any = {}
          newTxn.hash = burn.transaction.id
          newTxn.timestamp = burn.transaction.timestamp
          newTxn.type = TXN_TYPE.REMOVE
          newTxn.token0Amount = burn.amount0
          newTxn.token1Amount = burn.amount1
          newTxn.account = burn.sender
          newTxn.token0Symbol = updateNameData(burn.pair)?.token0?.symbol
          newTxn.token1Symbol = updateNameData(burn.pair)?.token1?.symbol
          newTxn.amountUSD = burn.amountUSD
          return newTxns.push(newTxn)
        })
      }
      if (list.swaps.length > 0) {
        list.swaps.map((swap: any) => {
          const netToken0 = swap.amount0In - swap.amount0Out
          const netToken1 = swap.amount1In - swap.amount1Out

          let newTxn: any = {}

          if (netToken0 < 0) {
            newTxn.token0Symbol = updateNameData(swap.pair)?.token0?.symbol
            newTxn.token1Symbol = updateNameData(swap.pair)?.token1?.symbol
            newTxn.token0Amount = Math.abs(netToken0)
            newTxn.token1Amount = Math.abs(netToken1)
          } else if (netToken1 < 0) {
            newTxn.token0Symbol = updateNameData(swap.pair)?.token1?.symbol
            newTxn.token1Symbol = updateNameData(swap.pair)?.token0?.symbol
            newTxn.token0Amount = Math.abs(netToken1)
            newTxn.token1Amount = Math.abs(netToken0)
          }

          newTxn.hash = swap.transaction.id
          newTxn.timestamp = swap.transaction.timestamp
          newTxn.type = TXN_TYPE.SWAP

          newTxn.amountUSD = swap.amountUSD
          newTxn.account = swap.to
          return newTxns.push(newTxn)
        })
      }
      const filtered = newTxns.filter((item) => {
        if (txFilter !== TXN_TYPE.ALL) {
          return item.type === txFilter && (filterText(item.token0Symbol, search) || filterText(item.token1Symbol, search))
        }
        return filterText(item.token0Symbol, search) || filterText(item.token1Symbol, search)
      })
      // @ts-ignore
      setFilteredItems(filtered)
      let extraPages = 1
      if (filtered.length % MAX_COUNT === 0) {
        extraPages = 0
      }
      if (filtered.length === 0) {
        setMaxPage(1)
      } else {
        setMaxPage(Math.floor(filtered.length / MAX_COUNT) + extraPages)
      }
    }
  }, [list, txFilter, search])

  const filteredList =
    filteredItems &&
    filteredItems
      .sort((a, b) => {
        return parseFloat(a[sortedColumn]) > parseFloat(b[sortedColumn])
          ? (sortDirection ? -1 : 1) * 1
          : (sortDirection ? -1 : 1) * -1
      })
      .slice(MAX_COUNT * (page - 1), page * MAX_COUNT)
    
  const handleName = (hash: string) => {
    window.open('https://bscscan.com/tx/' + hash)
  }
  const handleAccount = (account: string) => {
    window.open('https://bscscan.com/address/' + account)
  }

    const ListItem = ({ item } : { item: any }) => {
      return (
        <DashGrid style={{ height: '48px' }}>
          <DataText>
            <div onClick={() => handleName(item.hash)} style={{ color: '#2f80ed', marginLeft: below800 ? '0' : '20px', whiteSpace: 'nowrap' }}>
              {getTransactionType(item.type, item.token1Symbol, item.token0Symbol)}
            </div>
          </DataText>
          <DataText>
            {formattedNum(item.amountUSD, true)}
            {/* {currency === 'BNB' ? 'Ξ ' + formattedNum(item.valueETH) : formattedNum(item.amountUSD, true)} */}
          </DataText>
          {!below800 && (
            <>
              <DataText>
                {formattedNum(item.token1Amount) + ' '}{' '}
                {item.token1Symbol}
              </DataText>
              <DataText>
                {formattedNum(item.token0Amount) + ' '}{' '}
                {item.token0Symbol}
              </DataText>
            </>
          )}
          {!below1080 && (
            <DataText>
              <div onClick={() => handleAccount(item.account)} style={{ color: '#2f80ed'}}>
                {item.account && item.account.slice(0, 6) + '...' + item.account.slice(38, 42)}
              </div>
            </DataText>
          )}
          <DataText>{formatTime(item.timestamp)}</DataText>
        </DashGrid>
      )
    }

  return (
    <>
      {!below800 && <TabNav list={transactionNavList} state={txFilter} setState={setTxFilter}></TabNav>}
      <TableHead
        columns='1fr 1fr 1fr'
        SORT_FIELD={below800 ? SORT_FIELD_APP : SORT_FIELD}
        sortedColumn={sortedColumn}
        sortDirection={sortDirection}
        setSortedColumn={setSortedColumn}
        setSortDirection={setSortDirection}
      ></TableHead>

      <List p={0}>
        {filteredList &&
          filteredList.map((item: any, index: number) => {
            return (
              <StyleItem key={index}>
                <ListItem item={item} />
              </StyleItem>
            )
          })}
      </List>
    </>
  )
}

export default TransactionsTable
