import { ApolloClient } from 'apollo-client'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { HttpLink } from 'apollo-link-http'

export const client = new ApolloClient({
  link: new HttpLink({
    // uri: 'https://bsc-graph.mdex.com/subgraphs/name/chains/bsc',
    uri: 'https://graph.mdex.com/subgraphs/name/mdex/swap',
  }),
  cache: new InMemoryCache()
})

export const healthClient = new ApolloClient({
  link: new HttpLink({
    // uri: 'https://bsc-health.mdex.com/graphql',
    uri: 'https://health.mdex.com/graphql',
  }),
  cache: new InMemoryCache()
})

export const blockClient = new ApolloClient({
  link: new HttpLink({
    // uri: 'https://bsc-graph.mdex.com/subgraphs/name/mdex-blocks',
    uri: 'https://graph.mdex.com/subgraphs/name/mdex-heco-blocks',
  }),
  cache: new InMemoryCache()
})
