import detectEthereumProvider from '@metamask/detect-provider';
import { ethers } from 'ethers'
import ERC20ABI from '@src/constants/abi/erc20.json'

export const MaxUint256: string = "0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff";


export const setLocalAllowance = (tokenAddress: string, account: string, contractAddr: string, allowance=MaxUint256) => { // save allowance
  try {
    localStorage.setItem(`Allowanc-${tokenAddress.slice(0, 10)}${account.slice(0, 10)}${contractAddr.slice(0, 10)}`, allowance)
  } catch (error) {
    console.log('setLocalAllowance:', error)
  }
}

export const getLocalAllowance = (tokenAddress: string, account: string, contractAddr: string) => { // get allowance
  return localStorage.getItem(`Allowanc-${tokenAddress.slice(0, 10)}${account.slice(0, 10)}${contractAddr.slice(0, 10)}`)
}

// getProvide
export const getProvide = async() => {
  const provider = await detectEthereumProvider();
  return provider;
}

// getProvide 
export const connect = async() => {
  const ethereum:any = await getProvide()
  return ethereum.request({ method: 'eth_requestAccounts' }) // wait for package update
}

