import styled from 'styled-components'

import githubIcon from './images/github.png'
import mediumIcon from './images/medium.png'
import telegramIcon from './images/telegram.png'
import twitterIcon from './images/twitter.png'
import discordIcon from './images/discord.png'
import ecologicalFundsIcon from './images/ecologicalFunds.png'


const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 120px;
  background-color: #101523;
`
const StyleIcon = styled.div`
  width: 40px;
  border-radius: 20px;
`
const StyleText = styled.div`
  margin-left: 14px;
  color: #485266;
  font-size: 16px;
`
const Flex = styled.div`
  display: flex;
  align-items: baseline;
  height: 40px;
  margin-right: 40px;
  cursor: pointer;
`

export default function Footer () {
  const handleClick = (index: number) => {
    switch (index) {
      case 1:
        window.open('https://github.com/mdexSwap')
        break;
      case 2:
        window.open('https://medium.com/@mdex201229')
        break;
      case 3:
        window.open('https://t.me/MixDex')
        break;
      case 4:
        window.open('https://twitter.com/Mdextech')
        break;
      case 5:
        window.open('https://discord.com/invite/3TYDPktjqC')
        break;
    }
  }
  return (
    <Wrapper>
      <Flex onClick={() => handleClick(1)}>
        <StyleIcon>
          <img width="40px" src={githubIcon} alt="github"/>
        </StyleIcon>
        <StyleText>Github</StyleText>
      </Flex>
      <Flex onClick={() => handleClick(2)}>
        <StyleIcon>
          <img width="40px" src={mediumIcon} alt="medium"/>
        </StyleIcon>
        <StyleText>Medium</StyleText>
      </Flex>
      <Flex onClick={() => handleClick(3)}>
        <StyleIcon>
          <img width="40px" src={telegramIcon} alt="telegram"/>
        </StyleIcon>
        <StyleText>Telegram</StyleText>
      </Flex>
      <Flex onClick={() => handleClick(4)}>
        <StyleIcon>
          <img width="40px" src={twitterIcon} alt="twitter"/>
        </StyleIcon>
        <StyleText>Twitter</StyleText>
      </Flex>
      <Flex onClick={() => handleClick(5)}>
        <StyleIcon>
          <img width="40px" src={discordIcon} alt="discord"/>
        </StyleIcon>
        <StyleText>Discord</StyleText>
      </Flex>
      <Flex onClick={() => handleClick(6)}>
        <StyleIcon>
          <img width="40px" src={ecologicalFundsIcon} alt="ecologicalFunds"/>
        </StyleIcon>
        <StyleText>生态基金</StyleText>
      </Flex>
    </Wrapper>
  )
}
