import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'
import { Text } from 'rebass'
import Numeral from 'numeral'
import { ethers } from 'ethers'

import { blockClient } from '../apollo/client'
import { GET_BLOCK, GET_BLOCKS } from '../apollo/queries'

import { TimeframeOptions } from '../constants'

interface BasicData {
  token0?: {
    id: string
    name: string
    symbol: string
  }
  token1?: {
    id: string
    name: string
    symbol: string
  }
}

dayjs.extend(utc)

export function formatDate (time: number, format: string) {
  const timestamp = dayjs.unix(time)
  return dayjs(timestamp).format(format);
}

export function getTimeframe(timeWindow: any) {
  const utcEndTime = dayjs.utc()
  // based on window, get starttime
  let utcStartTime
  switch (timeWindow) {
    case TimeframeOptions.WEEK:
      utcStartTime = utcEndTime.subtract(1, 'week').endOf('day').unix() - 1
      break
    case TimeframeOptions.MONTH:
      utcStartTime = utcEndTime.subtract(1, 'month').endOf('day').unix() - 1
      break
    case TimeframeOptions.ALL_TIME:
      utcStartTime = utcEndTime.subtract(1, 'year').endOf('day').unix() - 1
      break
    default:
      utcStartTime = utcEndTime.subtract(1, 'year').startOf('year').unix() - 1
      break
  }
  return utcStartTime
}

export function getTimestampsForChanges() {
  const utcCurrentTime = dayjs()
  const t1 = utcCurrentTime.subtract(1, 'day').startOf('minute').unix()
  const t2 = utcCurrentTime.subtract(2, 'day').startOf('minute').unix()
  const tWeek = utcCurrentTime.subtract(1, 'week').startOf('minute').unix()
  return [t1, t2, tWeek]
}

export function formattedPercent(per: number, pure?: boolean) {
  const GREEN = '#03AD90'
  const RED = '#FF6971'
  let percent = parseFloat(per.toString())
  if (!percent || percent === 0) {
    return pure ? '0%' : <Text fontWeight={500}>0%</Text>
  }

  if (percent < 0.0001 && percent > 0) {
    return pure ? '< 0.0001%' : (
      <Text fontWeight={500} color={GREEN}>
        {'< 0.0001%'}
      </Text>
    )
  }

  if (percent < 0 && percent > -0.0001) {
    return pure ? '< 0.0001%' : (
      <Text fontWeight={500} color={RED}>
        {'< 0.0001%'}
      </Text>
    )
  }

  let fixedPercent = percent.toFixed(2)
  if (fixedPercent === '0.00') {
    return '0%'
  }
  let percentNum = parseFloat(fixedPercent)
  if (percentNum > 0) {
    if (percentNum > 100) {
      return pure ? `+${percent?.toFixed(0).toLocaleString()}%` : <Text fontWeight={500} color={GREEN}>{`+${percent?.toFixed(0).toLocaleString()}%`}</Text>
    } else {
      return pure ? `+${fixedPercent}%` : <Text fontWeight={500} color={GREEN}>{`+${fixedPercent}%`}</Text>
    }
  } else {
    return pure ? `${fixedPercent}%` : <Text fontWeight={500} color={RED}>{`${fixedPercent}%`}</Text>
  }
}

export const toK = (num: any) => {
  return Numeral(num).format('0.[00]a')
}

// using a currency library here in case we want to add more in future
export const formatDollarAmount = (num: number, digits: number) => {
  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: digits,
    maximumFractionDigits: digits,
  })
  return formatter.format(num)
}

export const formattedNum = (number: any, usd = false, unit= false) => {
  if (isNaN(number) || number === '' || number === undefined) {
    return usd ? '$0' : 0
  }
  let num = parseFloat(number)

  if (num > 500000000 || unit) {
    return (usd ? '$' : '') + toK(num.toFixed(0))
  }

  if (num === 0) {
    if (usd) {
      return '$0'
    }
    return 0
  }

  if (num < 0.0001 && num > 0) {
    return usd ? '< $0.0001' : '< 0.0001'
  }

  if (num > 1000) {
    return usd ? formatDollarAmount(num, 0) : Number(parseFloat(num.toString()).toFixed(0)).toLocaleString()
  }

  if (usd) {
    if (num < 0.1) {
      return formatDollarAmount(num, 4)
    } else {
      return formatDollarAmount(num, 2)
    }
  }

  return Number(parseFloat(num.toString()).toFixed(5)).toLocaleString()
}

export const isAddress = (value: any) => {
  try {
    return ethers.utils.getAddress(value.toLowerCase())
  } catch {
    return false
  }
}

/**
 * @notice 获取给定时间戳后的第一个块
 * @dev 查询速度通过将周期限制在600秒进行优化
 * @param {Int} timestamp in seconds
 */
 export async function getBlockFromTimestamp(timestamp: number) {
  let result = await blockClient.query({
    query: GET_BLOCK,
    variables: {
      timestampFrom: timestamp,
      timestampTo: timestamp + 600,
    },
    fetchPolicy: 'cache-first',
  })
  return result?.data?.blocks?.[0]?.number
}

/**
 * 获得两个值之间的标准变化百分比
 * @param {*} valueNow
 * @param {*} value24HoursAgo
 */
 export const getPercentChange = (valueNow: any, value24HoursAgo: any) => {
  const adjustedPercentChange =
    ((parseFloat(valueNow) - parseFloat(value24HoursAgo)) / parseFloat(value24HoursAgo)) * 100
  if (isNaN(adjustedPercentChange) || !isFinite(adjustedPercentChange)) {
    return 0
  }
  return adjustedPercentChange
}

/**
 * @notice 获取一个时间戳数组的块对象
 * @dev 块按时间顺序(ASC)返回，而不考虑输入
 * @dev blocks are returned at string representations of Int
 * @dev timestamps are returns as they were provided; not the block time.
 * @param {Array} timestamps
 */
 export async function getBlocksFromTimestamps(timestamps: any, skipCount = 500) {
  if (timestamps?.length === 0) {
    return []
  }
  let fetchedData = await splitQuery(GET_BLOCKS, blockClient, [], timestamps, skipCount)
  let blocks = []
  if (fetchedData) {
    for (var t in fetchedData) {
      // @ts-ignore
      if (fetchedData[t].length > 0) {
        blocks.push({
          timestamp: t.split('t')[1],
          // @ts-ignore
          number: fetchedData[t][0]['number'],
        })
      }
    }
  }
  return blocks
}

export async function splitQuery(query: any, localClient: any, vars: any, list: any, skipCount = 100) {
  let fetchedData = {}
  let allFound = false
  let skip: number = 0

  while (!allFound) {
    let end: number = list.length
    if (skip + skipCount < list.length) {
      end = skip + skipCount
    }
    let sliced = list.slice(skip, end)
    let result = await localClient.query({
      query: query(...vars, sliced),
      fetchPolicy: 'cache-first',
    })
    fetchedData = {
      ...fetchedData,
      ...result.data,
    }
    if (Object.keys(result.data).length < skipCount || skip + skipCount > list.length) {
      allFound = true
    } else {
      skip += skipCount
    }
  }

  return fetchedData
}

/**
 * 获取差异量加上变化本身的变化百分比(二阶变化)
 * @param {*} valueNow
 * @param {*} value24HoursAgo
 * @param {*} value48HoursAgo
 */
 export const get2DayPercentChange = (valueNow: any, value24HoursAgo: any, value48HoursAgo: any) => {
  // get volume info for both 24 hour periods
  let currentChange = parseFloat(valueNow) - parseFloat(value24HoursAgo)
  let previousChange = parseFloat(value24HoursAgo) - parseFloat(value48HoursAgo)

  const adjustedPercentChange = (parseFloat((currentChange - previousChange).toString()) / parseFloat(previousChange.toString())) * 100

  if (isNaN(adjustedPercentChange) || !isFinite(adjustedPercentChange)) {
    return [currentChange, 0]
  }
  return [currentChange, adjustedPercentChange]
}

const TOKEN_OVERRIDES: { [address: string]: { name: string; symbol: string } } = {
  '0xbb4cdb9cbd36b01bd1cbaebf2de08d9173bc095c': { // WETH
    name: 'Wrapped BNB',
    symbol: 'BNB',
  },
  // '0x1416946162b1c2c871a73b07e932d2fb6c932069': {
  //   name: 'Energi',
  //   symbol: 'NRGE',
  // },
}

// override tokens with incorrect symbol or names
export function updateNameData(data: BasicData): BasicData | undefined {
  if (data?.token0?.id && Object.keys(TOKEN_OVERRIDES).includes(data.token0.id)) {
    data.token0.name = TOKEN_OVERRIDES[data.token0.id].name
    data.token0.symbol = TOKEN_OVERRIDES[data.token0.id].symbol
  }

  if (data?.token1?.id && Object.keys(TOKEN_OVERRIDES).includes(data.token1.id)) {
    data.token1.name = TOKEN_OVERRIDES[data.token1.id].name
    data.token1.symbol = TOKEN_OVERRIDES[data.token1.id].symbol
  }

  return data
}

export const formatTime = (unix: any) => {
  const now = dayjs()
  const timestamp = dayjs.unix(unix)

  const inSeconds = now.diff(timestamp, 'second')
  const inMinutes = now.diff(timestamp, 'minute')
  const inHours = now.diff(timestamp, 'hour')
  const inDays = now.diff(timestamp, 'day')

  if (inHours >= 24) {
    return `${inDays} ${inDays === 1 ? 'day' : 'days'} ago`
  } else if (inMinutes >= 60) {
    return `${inHours} ${inHours === 1 ? 'hour' : 'hours'} ago`
  } else if (inSeconds >= 60) {
    return `${inMinutes} ${inMinutes === 1 ? 'minute' : 'minutes'} ago`
  } else {
    return `${inSeconds} ${inSeconds === 1 ? 'second' : 'seconds'} ago`
  }
}

export const filterText = (name: string, maxLength: number) => {
  let value = name
  if (name.length > maxLength) {
    value = name.slice(0, maxLength) + '...'
  }
  return value
}

export const formatUnit = (value: any, unit = true) => {
  const num = parseFloat(value)
  if (num > 10000) {
    return unit ? Numeral(num).format('0.[00]a') : Numeral(num).format('0.00')
  }
  if (num > 1000) {
    return Numeral(num).format('0.00')
  }
  if(num === 0 ) return 0.00
  if (num < 0.000001) return num.toFixed(10)
  if (num < 0.00001) return Numeral(num).format('0.000000000')
  if (num < 0.0001) return Numeral(num).format('0.00000000')
  if (num < 0.001) return Numeral(num).format('0.0000000')
  if (num < 0.01) return Numeral(num).format('0.000000')
  if (num < 0.1) return Numeral(num).format('0.00000')
  if (num < 1) return Numeral(num).format('0.0000')

  return Numeral(num).format('0.0000')
}