import styled from 'styled-components'
// import { useTranslation } from 'react-i18next'
import { Route, Switch } from 'react-router-dom'
import { ApolloProvider } from 'react-apollo'
import { client } from '../apollo/client'

import Markets from './market'
import Quotes from './h5/quotes'
import Exchange from './exchange'
import Transaction from './h5/transaction'
import Record from './h5/record'
import Info from './h5/info'

import 'antd/dist/antd.css'

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
`

const BodyWrapper = styled.div`
  width: 100%;
  overflow: auto;
  z-index: 1;
`

function App() {
  // const { t, i18n } = useTranslation()
  return (
    <ApolloProvider client={client}>
      <AppWrapper>
        <BodyWrapper>
          <Switch>
          <Route exact strict path='/' component={Markets} />
            <Route exact strict path='/quotes' component={Quotes} />
            <Route exact strict path='/market' component={Markets} />
            <Route exact strict path='/exchange/:id' component={Exchange} />
            <Route exact strict path='/transaction/:id/:mode' component={Transaction} />
            <Route exact strict path='/record' component={Record} />
            <Route exact strict path='/info/:id' component={Info} />
          </Switch>
        </BodyWrapper>
      </AppWrapper>
    </ApolloProvider>
  );
}

export default App;
