import { useParams } from 'react-router-dom'
import styled from 'styled-components'

import { Flex } from '../../../../components/Row'
import { usePairData } from '../../../../contexts/PairData'
import { formattedNum, formattedPercent } from '../../../../utils'

const InfoWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 16px;
`
const TextWrapper = styled.div`
  line-height: 26px;
  font-size: 26px;
  font-weight: bold;
  color: #03AD90;
`
const Text1Wrapper = styled.div`
  font-size: 12px;
  font-weight: 400;
  color: #7F8FAF;
`
const Text2Wrapper = styled.div`
  font-size: 12px;
  font-weight: 500;
  color: #48587B;
`
const ButtonApy = styled.div`
  margin-top: 14px;
  padding: 2px 6px;
  font-size: 16px;
  font-weight: bold;
  color: #FFFFFF;
  background: #03AD90;
  border-radius: 4px;
`
const TextApy = styled.div`
  font-size: 12px;
  font-weight: 500;
  color: #03AD90;
`

export default function Header () {
  const { id } = useParams<{id: string}>()
  const {
    reserveUSD,
    liquidityChangeUSD,
    oneDayVolumeUSD,
    volumeChangeUSD
  } = usePairData(id)

  const volume =  oneDayVolumeUSD && formattedNum(oneDayVolumeUSD, true)
  const formattedLiquidity = formattedNum(reserveUSD, true)
  const liquidityChange = liquidityChangeUSD && formattedPercent(liquidityChangeUSD)
  const fees = oneDayVolumeUSD && formattedNum(oneDayVolumeUSD * 0.003, true)
  const volumeChange = volumeChangeUSD && formattedPercent(volumeChangeUSD)

  return (
    <InfoWrapper>
        <div>
          <TextWrapper>{fees}</TextWrapper>
          <Flex>
            <ButtonApy>-6.36%</ButtonApy>
          </Flex>
        </div>
        <div>
          <Text1Wrapper>Liquidity</Text1Wrapper>
          <Text1Wrapper>Volume</Text1Wrapper>
          <Text1Wrapper>最高</Text1Wrapper>
          <Text1Wrapper>最低</Text1Wrapper>
          <Text1Wrapper>24H量</Text1Wrapper>
        </div>
        <div>
          <Text2Wrapper>{formattedLiquidity}</Text2Wrapper>
          <Text2Wrapper>355,345,340</Text2Wrapper>
          <Text2Wrapper>355,345,340</Text2Wrapper>
          <Text2Wrapper>355,345,340</Text2Wrapper>
          <Text2Wrapper>{volume}</Text2Wrapper>
        </div>
        <div>
          <TextApy>{liquidityChange}</TextApy>
          <TextApy>-10.75%</TextApy>
        </div>
      </InfoWrapper>
  )
}