import {
  LibrarySymbolInfo,
} from '../../../charting_library'
import { GET_KLINE_ACTION } from '../../../utils/api'

import mockData from '../mock'

const history: any = {}

export default {
  history,
  getBars: function (symbolInfo: LibrarySymbolInfo, resolution: string, from: number, to: number, firstDataRequest: boolean, token: string) {
    const platId = localStorage.getItem('platId') || 10000
    const trade = localStorage.getItem('trade') || 'btcusdt'
    const params = {
      symbol: trade,
      platId,
      period: 0
    }
    switch(true) {
      case resolution.indexOf('D') > 0 :
        const d = Number(resolution.substr(0, 1))
        params.period = 24*60*60*d
      break
      case resolution.indexOf('W') > 0 :
        const w = Number(resolution.substr(0, 1))
        params.period = w*24*60*60*7
      break
      case resolution.indexOf('M') > 0:
        const m = Number(resolution.substr(0, 1))
        params.period = m*24*60*60*30
      break
      default :
        params.period = Number(resolution)*60 
      break
    }
    // if (firstDataRequest) {
    //   var lastBar = mockData[mockData.length - 1]
    //   history[symbolInfo.name] = {
    //     lastBar
    //   }
    // }
    return new Promise((resolve, reject) => {
      // resolve(mockData.reverse)
      GET_KLINE_ACTION({
        pair_token: token,
        period: 60
      }).then((list: any) => {
        var bars = list.map((item: any) => {
          // el：[ts, 开, 高, 低, 收, 量]
          let time = item[0]
          return {
            time, //TradingView requires bar time in ms
            open: parseFloat(item[1]),
            high: parseFloat(item[2]),
            low: parseFloat(item[3]),
            close: parseFloat(item[4]),
            volume: parseFloat(item[5])
          }
        })
        bars.reverse()
        if (firstDataRequest) {
          var lastBar = bars[bars.length - 1]
          history[symbolInfo.name] = {
            lastBar
          }
        }
        resolve(bars)
      })
    })
  }
}
