import { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useParams } from 'react-router-dom'

import Header from '../../components/Header'
import TabSearch from '../../components/TabSearch'
import TabNav from './components/TabNav'
import Table from './components/LeftList'
import MainContent from './components/Main'
import DealList from './components/DealList'
import Modal from '../../components/Modal'

import { useAllPairData, usePairTransactions } from '../../contexts/PairData'
import { useUserTransactions } from '../../contexts/User'
import { useUserAccount } from '@src/contexts/User'
import { GET_COLLECT_LIST_ACTION, GET_RECENT_TRANSACTION_ACTION, GET_PAIRS_LIST_ACTION } from '../../utils/api'

import submiticon from '../../images/arrow-submit.png'

const Wrapper = styled.div`
  position: relative;
  min-width: 1440px;
  width: 100%;
  height: 100vh;
  background-color: #07082a;
  overflow: scroll;
`
const BodyWrapper = styled.div`
  display: flex;
  width: 100%;
  height: calc(100% - 85px);
  margin-top: 85px;
`
const LeftContent = styled.div`
  width: 20%;
  max-width: 360px;
  margin-right: 10px;
  flex-shrink: 0;
  background-color: #060C24;
`

const RightContent = styled.div`
  margin-left: 10px;
  margin-right: 16px;
  background-color: #060C24;
`

const StyleIcon = styled.div`
  margin-top: 86px;
  margin-bottom: 48px;
  text-align: center;
  font-size: 0;
`
const Text1 = styled.div`
  font-size: 20px;
  color: #10283F;
  text-align: center;
`
const Text2 = styled.div`
  margin-top: 8px;
  font-size: 16px;
  color: #03AD90;
  text-align: center;
`
const BtnWrapper = styled.div`
  width: 354px;
  height: 66px;
  line-height: 66px;
  margin: 30px auto 20px auto;
  background: #03AD90;
  box-shadow: 0px 10px 10px 0px rgba(1, 207, 170, 0.2);
  border-radius: 10px;
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
  cursor: pointer;
`

const list = ['全部', 'BTC', 'USDT', 'ETH', 'HT', 'BUSD', 'GBP', 'HUSD']
// const ACCOUNT = '0x99D0aDb61C345fF9545D17E8b4Da16B375D5fc00'

export default function Exchange () {
  const account = useUserAccount()
  const [ tab, setTab ] = useState(list[0])
  const [filterList, setFilterList] = useState([])
  const [collectList, setCollectList] = useState([])
  const [search, setSearch] = useState('')

  const [collect, setCollect] = useState(false)

  const [allPairs, setAllPairs] = useState<any>([])
  const [active, setActive] = useState(1)
  const [state, setState] = useState(0)

  const [realPrice, setRealPrice] = useState(0)
  useEffect(() => {
    // Modal({
    //   props: {
    //     width: '400px',
    //     showClose: true
    //   },
    //   content: (<div>
    //     <StyleIcon>
    //       <img width='96px' src={submiticon} alt='submit'/>
    //     </StyleIcon>
    //     <Text1>交易已提交</Text1>
    //     <Text2>View on HECO</Text2>
    //     <BtnWrapper>Close</BtnWrapper>
    //   </div>)
    // })
  }, [])

  useEffect(() => {
    getCollect()
  }, [allPairs, active, collect, account])

  useEffect(() => {
    GET_PAIRS_LIST_ACTION({
      page: 1
    }).then((list: any) => {
      setAllPairs(list.rows)
      console.log('pairs----', list)
    })
  }, [])

  useEffect(() => {
    let newList: any = {}
    allPairs.map((item: any) => {
      if (tab === '全部') {
        newList = allPairs
        return
      }
      if (item.token0_symbol === tab || item.token1_symbol === tab) {
        newList.push(item)
      }
    })
    setFilterList(newList)
  }, [tab])

  function getCollect () {
    GET_COLLECT_LIST_ACTION({
      account: account
    }).then((list: any) => {
      let newList: any = []
      let pairList = JSON.parse(JSON.stringify(allPairs))
      list.map((item: any) => {
        allPairs.map((pair: any, index: number) => {
          if(pair.pair_token === item.address) {
            pair.collect = true
            newList.push(pair)
            pairList[index].collect = true
          }
        })
      })
      setFilterList(pairList)
      setCollectList(newList)
    })
  }

  return (
    <>
      <Wrapper>
        <Header></Header>
        <BodyWrapper>
          <LeftContent>
            <TabSearch state={active} setState={setActive} options={['自选', '现货']} onSearch setSearch={setSearch}></TabSearch>
            {active === 1 && <TabNav list={list} state={tab} setState={setTab} dark></TabNav>}
            {/* @ts-ignore */}
            {active === 0 && <Table pairs={collectList} search={search}/>}
            {/* @ts-ignore */}
            {active === 1 && <Table pairs={filterList} search={search} />}
          </LeftContent>
          <MainContent realPrice={realPrice} account={account} collectList={collectList} collect={collect} setCollect={setCollect}></MainContent>
          <RightContent>
            <TabSearch state={state} setState={setState} options={['最近成交', '我的成交']} fontSize='12px' setSearch={setSearch}></TabSearch>
            <DealList state={state} account={account} setRealPrice={setRealPrice}></DealList>
          </RightContent>
        </BodyWrapper>
      </Wrapper>
    </>
  )
}
