      //     const oneDayVolumeUSDB = pairB.oneDayVolumeUSD ? pairB.oneDayVolumeUSD : pairB.oneDayVolumeUntracked
      //     const reserveUSD = pairA.oneDayVolumeUSD ? pairA.trackedReserveUSD : pairA.reserveUSD
      //     const reserveUSDB = pairB.oneDayVolumeUSD ? pairB.trackedReserveUSD : pairB.reserveUSD

      //     const apy0 = parseFloat(oneDayVolumeUSD * 0.003 * 356 * 100) / parseFloat(reserveUSD)
      //     const apy1 = parseFloat(oneDayVolumeUSDB * 0.003 * 356 * 100) / parseFloat(reserveUSDB)
      //     return apy0 > apy1 ? (sortDirection ? -1 : 1) * 1 : (sortDirection ? -1 : 1) * -1
      //   }
