export default function RecordList ({ hideHead } : { hideHead?: boolean }) {
  const history = useHistory()
  const account = useUserAccount()
  const { id } = useParams<{id: string}>()

  const [userList, setUserList] = useState([])

  useEffect(() => {
    // 获取我的交易记录
    account && getUserList(50).then((data: any) => {
      data.length && setUserList(data)
    })
  }, [account, id])

  function getUserList (limit: number) {
    return GET_USER_TRANSACTION_ACTION({
      pair_token: id,
      limit: limit,
      addr: account
      // pair_token: '0xfbe7b74623e4be82279027a286fa3a5b5280f77c',
      // limit: limit,
      // addr: '0x097a2f280ce5da160f44cf9d6734438acb7723b0'
    })
  }
  const listNode = () => {
    return userList.map((item: any, index: number) => {
      const value = item.amount1In - item.amount1Out
      const isBuy = item.side === 'BUY'

      return (
        <ListItem key={index}>
          <Flex justify='space-between'>
            <ContentWrapper>
              <TypeWrapper sale={!isBuy}>{ isBuy ? '买' : '卖'}</TypeWrapper>
              <NameWrapper>BTC/USDT</NameWrapper>
            </ContentWrapper>
            <DateWrapper>{item.time && formatDate(item.time, 'MM-DD HH:mm:ss') || '--:--:--'}</DateWrapper>
            <StatusWrapper status="#2C72F4">成功</StatusWrapper>
          </Flex>
          <Flex marginTop='8px'>
            <ContentWrapper>
              <Text1>成交量</Text1>
            </ContentWrapper>
            <DateWrapper>成交单价</DateWrapper>
          </Flex>
          <Flex marginTop='2px'>
            <ContentWrapper>
              <Text2>{formattedNum(item.amount)}</Text2>
            </ContentWrapper>
            <Text2>{formattedNum(item.price)}</Text2>
          </Flex>
          <CheckInfo>
            <Text>查看Hceo</Text>
            <img width='12px' height='12px' src={ShareIcon} alt="share"/>
          </CheckInfo>
        </ListItem>
      )
    })
  }
  return(
    <>
      {!hideHead && <Head>
        <div>交易记录</div>
        <img onClick={() => history.push('/record')} width='18px' height='19px' src={recordIcon} alt="record"/>
      </Head>}
      <Wrapper>
        {!userList.length && <WrapperEmpty>
          <img width='61px' src={EmptyIcon} alt="empty"/>
          <TextWrapper>暂无记录</TextWrapper>
        </WrapperEmpty>}
        {!!userList.length && listNode()}
      </Wrapper>
    </>
  )
}