import { useEffect, useState } from 'react';
import {
	widget,
	ChartingLibraryWidgetOptions,
	LanguageCode,
	ResolutionString
} from '../../charting_library';
import Datafeed, { onCallback } from './api'

function getLanguageFromURL(): LanguageCode | null {
	const regex = new RegExp('[\\?&]lang=([^&#]*)');
	const results = regex.exec(window.location.search);
	return results === null ? null : decodeURIComponent(results[1].replace(/\+/g, ' ')) as LanguageCode;
}

export function TVChartContainer ({ symbol, token, interval, light } : {
	symbol: string,
	token: string,
	interval: string,
	light?: boolean
}) {
	const [tvWidget, setTvWidget] = useState<any>()
	const [chartLoad, setChartLoad] = useState(false)

	useEffect(() => {
		const widgetOptions: ChartingLibraryWidgetOptions = {
			symbol: symbol,
			// @ts-ignore
			datafeed: Datafeed,
			interval: interval as ChartingLibraryWidgetOptions['interval'],
			container_id: 'tv_chart_container' as ChartingLibraryWidgetOptions['container_id'],
			library_path: '/charting_library/' as string,
			theme: light ? 'Light' : 'Dark',

			locale: 'zh' || getLanguageFromURL(),
			disabled_features: [ // 隐藏组件
				'header_widget', // 头部
				'use_localstorage_for_settings',
				'left_toolbar',
				'display_market_status',
				'timeframes_toolbar', // 底部时间
				'control_bar', // 控制栏
				'main_series_scale_menu', //设置按钮
				'edit_buttons_in_legend',
				// 'legend_widget'
			],
			enabled_features: [
			],
			timeframe: '1W',
			charts_storage_url: 'https://saveload.tradingview.com',
			charts_storage_api_version: '1.1',
			client_id: 'tradingview.com',
			user_id: 'public_user_id',
			fullscreen: false,
			autosize: true,
			studies_overrides: {
				'volume.volume.color.0': 'rgba(255, 105, 113, 1)',
				'volume.volume.color.1': 'rgba(1, 207, 170, 1)',
			},
			overrides: {
				// 'paneProperties.background': '#060C24',
				// 'paneProperties.vertGridProperties.color': '#060C24',
				// 'paneProperties.horzGridProperties.color': '#2e3b44',
				
				'mainSeriesProperties.candleStyle.upColor': '#01CFAA',
				'mainSeriesProperties.candleStyle.downColor': '#FF6971',
				// "mainSeriesProperties.style": 1 // k线类型
			},
			timezone: 'Asia/Chongqing'
		};

		let tvWidget = new widget(widgetOptions);
		setTvWidget(tvWidget)

		tvWidget.onChartReady(() => {
			setChartLoad(true)
			const studies = tvWidget.chart().getAllStudies();
			const studiesEl = tvWidget.chart().getStudyById(studies[0].id)
			// 取消指标向上合并
			studiesEl.unmergeDown()
			studiesEl.setUserEditEnabled(false)

			tvWidget.chart().createStudy("Moving Average", true, true, [5], { 'precision': 6, "plot.color.0" : "#965fc4" });
			tvWidget.chart().createStudy("Moving Average", true, true, [10], { 'precision': 6, "plot.color.0" : "#55b263" });
			tvWidget.chart().createStudy("Moving Average", true, true, [30], { 'precision': 6, "plot.color.0" : "#b7248a" });
			// tvWidget.chart().crossHairMoved(({price, time}) => {
			// 	console.log('price---', price)
			// 	console.log('time---', time)
			// })
		});

		return () => {
			tvWidget.remove();
		}
	}, [symbol])

	useEffect(() => {
		changeTime(interval as ResolutionString)
	}, [interval])
	useEffect(() => {
		onCallback()
		chartLoad && tvWidget.activeChart().resetData()
	}, [token])

	const changeTime = (time: ResolutionString) => {
		if (tvWidget && tvWidget.chart && chartLoad) {
			tvWidget.chart().refreshMarks()
			localStorage.setItem('localResolutions', time)
			tvWidget.chart().setResolution(time, () =>{})
		}
	}

	return (
		<div
			style={{height: '100%'}}
			id='tv_chart_container'
		/>
	)
}
